body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face{
  font-family:'sketch';
  src:url(/memeGenerator/static/media/sketch.35c4ac28.ttf);
}

.App {
  width:100%;
  margin:0;
  padding:0;
  height: 100vh;
overflow-y: auto;
position: relative;
background-color: rgb(255, 255, 255);

}
.titleBox{
  margin-bottom: 70px;
}

.title{
font-family:'sketch';
color:rgb(0, 0, 0);
text-align: center;
letter-spacing: 4px;
text-shadow: 3px 2px 3px rgb(72, 255, 0);
-webkit-transform: rotate(-10deg);
        transform: rotate(-10deg);
font-size: 50px;
cursor: pointer;

}
.title:hover{
  color:blue;
}

.bigBox{
display:-webkit-flex;
display:flex;
-webkit-flex-flow: row wrap;
        flex-flow: row wrap;
-webkit-justify-content: space-around;
        justify-content: space-around;

}

.smallBox{
width:400px;
height: 390px;
background-color: rgb(88, 58, 50);
position: relative;
margin-bottom:30px;
cursor: pointer;
box-shadow: 2px 2px 3px black;
transition: all .4s ;

}
.beSmaller{
width:380px;
height: 370px;
}
.smallBox img {
  width:100%;
  height: 100%;
  cursor: pointer;
}

.innerdiv{
  width:100px;
  height: 100px;
  background-color: rgba(137, 43, 226, 0.274);
}


.page{
    position: fixed;
   top:0;
   width:100%;
   height: 100%; 
   background-color: rgba(10, 10, 10, 0.37);
   
}
.box{
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width: 400px;
    height: 600px;
    background-color: white;
    border:2px solid grey;
    box-shadow: 2px 2px 5px 3px rgba(0, 0, 0, 0.274);
    background-color: rgb(255, 255, 255);
}
.xIcon {
   
    margin-top:10px;
    position: absolute;
    left:93%;
    font-weight: bold;
    cursor: pointer;
    display:inline-block;
    width:15px;
    height: 15px;
    transition: .7s all ease;
}
.xIcon:hover{
    color: rgb(190, 201, 39);
    font-size:18px;


}

.tinyBox{
    position: relative;
    top:6%;
    left: 13%;
    background-color: blueviolet;
    width:300px;
    height: 300px;

}
.tinyBox2{

    width:300px;
    height: 300px;
    background-color: white;

}
.tinyBox img{
    width: 100%;
    height: 100%;

}

.upper{
    position :absolute;
    top:60%;
    left:12%;


}
.lower{
    position :absolute;
    top:75%;
    left:12%;
}
.upper span ,.lower span {
font-family: 'Georgia';
font-weight: bold;
letter-spacing: 0.5px;
color: rgb(61, 24, 24);
display:block;

}
#upperText,#lowerText {
    -webkit-appearance: none;
    border:2px solid rgb(253, 253, 253);
        box-shadow: 2px 2px 5px 3px rgba(0, 0, 0, 0.274);
    height: 30px;
    width:300px;
    font-family: monospace;
    font-weight: bold;
    font-size:15px;
margin:10px;
}

.download button {
    -webkit-appearance: none;
    position :absolute;
    top:90%;
    left:26%;
    width:200px;
    height: 40px;
    background-color: rgba(0, 0, 0, 0.479);
    color:white;
    border:2px solid rgb(253, 253, 253);
    box-shadow: 2px 2px 5px 3px rgba(0, 0, 0, 0.274);
    font-size:16px;
    letter-spacing: 0.5px;
    cursor: pointer;

    
}

.download button:hover{
    background-color: rgb(190, 201, 39);
}
